    left: 0;
    background-color: #eeeeee;
    -webkit-transform:scaleX(0.4);
            transform:scaleX(0.4);
    transition: 0.15s;
}

.Navbar_navList__36ZuU li:first-child a::after{
    font-size: 2.5em;
    position:absolute;
    left:40px;
    display: none;
}

.Navbar_navList__36ZuU li a:hover{
    color:#fff;
}

.Navbar_navList__36ZuU li a:hover::after{
    -webkit-transform: scaleX(1);
            transform: scaleX(1);
